<template>
  <nav class="main-nav">
    <div class="logo" @click="navToHome">
      <img src="../img/logo.png" />
    </div>
    <div class="nav-items">
      <router-link to="/activity/add"><font-awesome-icon :icon="['fa', 'plus']" /><span style="margin-left:7px">Create an activity</span></router-link>
      <div class="seprator"></div>
      <router-link v-if="!user" to="/login"><font-awesome-icon :icon="['fa', 'sign-in-alt']" /><span style="margin-left:7px">Login</span></router-link>
      <div class="logout" v-else @click="doLogout"><font-awesome-icon :icon="['fa', 'sign-out-alt']" /><span style="margin-left:7px">Logout</span></div>
    </div>
  </nav>
</template>

<script>
export default {
  methods: {
    navToHome() {
      if (this.$route.path !== "/") this.$router.push("/");
    },
    async doLogout() {
      await this.$store.dispatch({ type: "logout" });
      this.$router.push("/");
    }
  },
  computed: {
    user() {
      return this.$store.getters.loggedinUser;
    }
  }
};
</script>

<style lang="scss">
.seprator {
  border-left: 1px solid gray;
  display: block;
  height: 15px;
  margin: 0.75rem;
  padding: 0;
}

.logo {
  cursor: pointer;
  display: flex;
  font-size: bold;
  align-items: center;
  img {
    width: 50px;
  }
  .title {
    font-size: 1.3rem;
    margin-left: 10px;
  }
}
.main-nav {
  padding: 5px 15px;
  user-select: none;
  border-bottom: 2px solid rgb(228, 228, 228);
  display: flex;
  align-items: center;
  justify-content: space-between;
  .nav-items {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-width: 150px;
  }
  a,
  .logout {
    cursor: pointer;
    font-size: 0.8rem;
    color: #2c3e50;
    text-decoration: none;
    transition: all 0.1s;
    &.router-link-exact-active {
      color: #42b983;
    }
    &:hover {
      color: #42b983;
    }
  }
}
</style>
